header {
	width: 100%;
	display: flex;
	height: 60px;
	background-color: rgba(30, 30, 30, 1);
}

header .center {
	width: 90%;
	min-width: 1500px;
	max-width: 1700px;
	display: flex;
	justify-content: space-between;
}

header .center h1 {
	width: 200px;
	color: white;
	line-height: 60px;
}

header .center ul {
	width: 700px;
	display: flex;
	color: white;
	justify-content: space-between;
}

header .center ul li {
	line-height: 60px;
	cursor: pointer;
}

.insta {
	color: #fff;
	line-height: 60px;
}

.youtube {
	color: #fff;
	line-height: 60px;
}
